import React from "react";

import '../css/price.css'

interface PriceInfoProps {
    title: string;
    liltitleOne: string;
    textOne: string;
    liltitleTwo: string;
    textTwo: string;
    liltitleThree: string;
    textThree: string;
    liltitleFour: string;
    textFour: string;
    liltitleFive: string;
    textFive: string;
}
const PriceInfo: React.FC<PriceInfoProps> = (props) => {
    return (
        <div className="text-container">
            <h2>{props.title}</h2>

            <h3>{props.liltitleOne}</h3>
            <p>{props.textOne}</p>

            <h3>{props.liltitleTwo}</h3>
            <p>{props.textTwo}</p>

            <h3>{props.liltitleThree}</h3>
            <p>{props.textThree}</p>

            <h3>{props.liltitleFour}</h3>
            <p>{props.textFour}</p>

            <h3>{props.liltitleFive}</h3>
            <p>{props.textFive}</p>
        </div>
    );
}

const PriceInfoContainer: React.FC = () => {
    const title = '    ?';

    const liltitleOne = ' ';
    const textOne = '      ,    .   ,            ,   ,    .  ,   ))';

    const liltitleTwo = '  ';
    const textTwo = '       . ,    ,              .';

    const liltitleThree = ' ';
    const textThree = '   ,       .  ,   ,    ,  ,    ..';

    const liltitleFour = '   -';
    const textFour = '         .   :     , ,   , , , ,   .            .';

    const liltitleFive = '   ';
    const textFive = '               .            ,         10$.';

    return(
        <PriceInfo
        title={title}
        liltitleOne={liltitleOne}
        textOne={textOne}
        liltitleTwo={liltitleTwo}
        textTwo={textTwo}
        liltitleThree={liltitleThree}
        textThree={textThree}
        liltitleFour={liltitleFour}
        textFour={textFour}
        liltitleFive={liltitleFive}
        textFive={textFive}
        />
    );
}
export default PriceInfoContainer;
