import React, { useState } from "react";

import '../css/price.css'

const PriceForm: React.FC = () => {
    const [length, setLength] = useState<number | string>('');
    const [width, setWidth] = useState<number | string>('');
    const [bodyPart, setBodyPart] = useState<string>('');
    const [price, setPrice] = useState<number | null>(null);

    const bodyPartPrices: { [key: string]: number } = {
        arm: 100,
        leg: 120,
        back: 150,
        chest: 140,
    };

    const calculatePrice = () => {
        const area = (Number(length) * Number(width)) / 100; //    
        const basePrice = bodyPartPrices[bodyPart] || 0;
        const totalPrice = area * basePrice;
        setPrice(totalPrice);
    };

    return (
        <div className="price-form">
            <h2>  </h2>
            <div>
                <label>
                     ():
                    <input
                        type="number"
                        value={length}
                        onChange={(e) => setLength(e.target.value)}
                    />
                </label>
            </div>
            <div>
                <label>
                     ():
                    <input
                        type="number"
                        value={width}
                        onChange={(e) => setWidth(e.target.value)}
                    />
                </label>
            </div>
            <div>
                <label>
                     :
                    <select
                        value={bodyPart}
                        onChange={(e) => setBodyPart(e.target.value)}
                    >
                        <option value="">  </option>
                        <option value="arm"></option>
                        <option value="leg"></option>
                        <option value="back"></option>
                        <option value="chest"></option>
                    </select>
                </label>
            </div>
            <button onClick={calculatePrice}> </button>
            {price !== null && (
                <div className="price-result">
                    <h3> : {price.toFixed(2)} .</h3>
                </div>
            )}
        </div>
    );
};

export default PriceForm;
